var _ = require('lodash')
export default {
  // name: 'AddItem',
  props: {},
  data() {
    return {
      item: {
        category: null,
        recordType: 1,
        amount: 0,
        memo: '',
        desc: '',
        datetime: Date.now()
      },
      categoryData: [],
    }
  },
  mounted() {
    this.$http.post('api/getCategories').then(response => {
      let res = response.body;
      if (res.Categories && res.Categories.length > 0) {
        this.categoryData = res.Categories;
      } else {
        //error
      }
    })
  },
  methods: {
    checkData() {
      if (!this.item.category) {
        this.$message({
          message: '类型不能为空！',
          type: 'warning',
          // duration: 3000
        });
        return false;
      }
      if (this.item.amount == 0) {
        this.$message({
          message: '金额不能为空！',
          type: 'warning',
          // duration: 3000
        });
        return false;
      }
      if (!this.item.datetime) {
        this.$message({
          message: '时间不能为空！',
          type: 'warning',
          // duration: 3000
        });
        return false;
      }
      if (!this.item.desc) {
        this.$message({
          message: '描述不能为空！',
          type: 'warning',
          // duration: 3000
        });
        return false;
      }
      return true;
    },
    onSubmit() {
      if (this.checkData()) {
        this.$http.post('/api/save', { item: this.item }).then(response => {
          let res = response.body;
          if(res.Success){
            this.$message({
              message:'新建成功！',
              type:'success',
              // duration: 3000
            });
            // this.reset();
          }
        })
      }
    },
    reset() {
      this.item = {
        category: null,
        recordType: 1,
        amount: 0,
        memo: '',
        desc: '',
        datetime: Date.now()
      }
    }
  }
}




